import React ,{useState,useEffect} from "react";
import https from "https";
import Card from "./Card.jsx";
import Header from "./Header.jsx";
import LinechartWorld from "./LinechartWorld";



const Worlddata= ()=>{

  const [worlddata, setworlddata ]=useState([{}]);


  const url="https://covidapi.info/api/v1/global";


      useEffect(function(){


        https.get(url,function(response){
            
          console.log(response.statusCode);

            response.on("data",(data)=>{
                
                const jsObj=JSON.parse(data);
                console.log(jsObj);
                var obj=[];
                var y=0;
                var date=jsObj.date;
                for(var x in jsObj.result)
                    obj.push({date:date,title:'TOTAL '+x.toUpperCase() ,cases:jsObj.result[x].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")});    
                  console.log(obj);
                  setworlddata(obj);
            });
      
        });
        
    },[url]);
  
    const createCard=(entry)=>
    {
        return (
          <Card
            key={entry.id}
            date={entry.date}
            title={entry.title}
            case={entry.cases}
          />
        )
    };

      return(
        <div className="container">
          <Header add="World Info" />
          <div className="row">
            {worlddata.map(createCard)}
          </div>
          <LinechartWorld />
        </div>
      )
};

export default Worlddata;