import { Line } from "react-chartjs-2";
import React ,{useState,useEffect} from "react";
import https from "https";


var url='https://covidapi.info/api/v1/global/count';


const LinechartWorld=()=> {

    const [dataObj, setdataObj ]=useState([]);

    useEffect(()=>{

        https.get(url,(response)=>{
            
          console.log(response.statusCode);
  
            response.on("data",(data)=>{
                
                var jsObj=JSON.parse(data);
                var xLabel=[];
                var confirmed=[];
                var deaths=[];
                var recovered=[];

                  for(var x in jsObj.result) 
                  {
                      xLabel.push(x);
                      confirmed.push(jsObj.result[x].confirmed);
                      deaths.push(jsObj.result[x].deaths);
                      recovered.push(jsObj.result[x].recovered);
                  }    

                  setdataObj([xLabel,confirmed,deaths,recovered]);
            });
        });
  
      },[url]);
  

    const data={
        labels:dataObj[0],
        datasets:[
            {
                label:'confirmed',
                data:dataObj[1],
                borderColor:'red',
                backgroundColor:'red'
            },
            {
                label:'deaths',
                data:dataObj[2],
                borderColor:'black',
                backgroundColor:'black'
            },
            {
                label:'Recovered',
                data:dataObj[3],
                borderColor:'Green',
                backgroundColor:'green'
            }
        ]
    }

    return(

        <Line data={data}  />
    )
}

export default LinechartWorld;