import React from 'react';
import Worlddata from "./Worlddata.jsx"
import Countrydata from "./Countrydata.jsx";
import Basictips from './Basictips';
import Nav from "./Nav";
import {HashRouter as Router ,Route} from "react-router-dom";


const App=()=>{
    return(
        <div>
      <Router>
        <Nav />
        <Route exact path="#/" component={Worlddata}/>
        <Route path="/CountryData" component={Countrydata}/>
        <Route path="/BasicTips" component={Basictips} />
      </Router>
    </div>  
    
    )
}

export default App;