import React, { useState ,useEffect, useRef} from "react";
import https from "https";
import Select from 'react-select';



const Picker= (props)=> 
{
    const url="https://restcountries.eu/rest/v2/all";  
    const temp=useRef([]);

    useEffect(function(){

        https.get(url,(res)=> {
            
            res.on("data",(data)=> {

                let jsobj=JSON.parse(data);

                jsobj.map((x)=>{
                    var {name,alpha3Code,flag}=x;
                    temp.current.push({label:name,value:[name,alpha3Code,flag]});    
                });

               console.log(temp)
            }) 
        })

    },[url]);
    
   
    return (
        <div className="picker">
            <Select  
                options={temp.current} 
                onChange={props.onChange}    
            />

        </div>
    )
}

export  {Picker};