import React ,{useState,useEffect,useRef} from "react";
import https from "https";
import Header from "./Header.jsx";
import { Picker } from "./Picker";
import Card from "./Card";
import LinechartCountry from "./LinechartCountry";


const Countrydata=()=>{

    const [countrydata, setcountrydata ]=useState([{}]);
    
    const [countryUrl, setcountryUrl] = useState("https://covidapi.info/api/v1/country/IND");

    const [url, seturl] = useState('https://covidapi.info/api/v1/country/'+"IND"+'/latest');
    
    var countryName=useRef("India")
    
    var countryFlag=useRef("https://restcountries.eu/data/ind.svg")
   
        useEffect(()=>{

          https.get(url,(response)=>{
              
           // console.log(response.statusCode);
              if(response.statusCode!=200)
              {
                setcountrydata([{date:null,title:"Confirmed",case:"No data available"},{date:null,title:"Deaths",case:"No data available"},{date:null,title:"Recovered",case:"No data available"}]);    
              }
              else{
                response.on("data",(data)=>{
                    
                  console.log("hi");
                    var jsObj=JSON.parse(data);
                    var info=0;
                    var date=0;
                      for(var x in jsObj.result)
                          date=x;    
                      info=jsObj.result[x];
                      var obj=[];
                      for(var y in info)
                      {
                        obj.push({date:date, title:"TOTAL "+y.toUpperCase(), case:info[y].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")});
                      }

                      setcountrydata(obj);
                });
            }
          });

      },[url]);

   
      const eventHandler=(e)=>
      {
          console.log(e.value);
          var countryISO=e.value[1];
          countryName.current=e.value[0];
          countryFlag.current=e.value[2];
          seturl('https://covidapi.info/api/v1/country/'+countryISO+'/latest');
          setcountryUrl('https://covidapi.info/api/v1/country/'+countryISO);
      }
     

      const createCard=(entry)=>
      {
        return (
          <Card
            key={entry.id}
            date={entry.date}
            title={entry.title}
            case={entry.case}
          />
          )
      };


     return(
        <div >
          <div className="countryHead">
            <Header add={countryName.current}/>
            <img className="flag" src={countryFlag.current} />
          </div>
          
          <div className="container">
            <div className="row">
            {countrydata.map(createCard)}
          </div>
          
          <Picker onChange={eventHandler}/>
          <LinechartCountry url={countryUrl}/>
          </div>
          </div>  
        )
  };

  export default Countrydata;
      