import { Line } from "react-chartjs-2";
import React ,{useState,useEffect} from "react";
import https from "https";

const LinechartCountry=(props)=>{

    const [dataObj, setdataObj ]=useState([]);

    useEffect(()=>{

        https.get(props.url,(response)=>{
            
       //   console.log(response.statusCode);
        if(response.statusCode!=200)    
        {
            setdataObj([[],[],[],[]])
        }
        else{
            response.on("data",(data)=>{
                
                var jsObj=JSON.parse(data);
                var xLabel=[];
                var confirmed=[];
                var deaths=[];
                var recovered=[];
                var count=jsObj.count-180;
                var i=1;
                  for(var x in jsObj.result) 
                    {
                        xLabel.push(x);
                        confirmed.push(jsObj.result[x].confirmed);
                        deaths.push(jsObj.result[x].deaths);
                        recovered.push(jsObj.result[x].recovered);
                    }    

                  setdataObj([xLabel,confirmed,deaths,recovered]);
                });
            }    
        });
  
      },[props.url]);
  

    const data={
        labels:dataObj[0],
        datasets:[
            {
                label:'confirmed',
                data:dataObj[1],
                borderColor:'red',
                backgroundColor:'red'
            },
            {
                label:'deaths',
                data:dataObj[2],
                borderColor:'black',
                backgroundColor:'black'
            },
            {
                label:'Recovered',
                data:dataObj[3],
                borderColor:'Green',
                backgroundColor:'green'
            }
        ]
    }

    return(
            <Line 
                data={data}
            /> 

    )
}

export default LinechartCountry;